@import "../core/Variables.scss";
.warning{
    display: flex;
    flex-direction: column;
    align-items: center;

    &__text {
        margin-top: 80px;
        font-size: 40px;
        letter-spacing: 2px;
    }
    &__link {
        font-size: 25px;
        text-decoration: none;
        color: $color-card;
        margin-top: 30px;
        margin-bottom: 80px;
        background-color: #ADE8A9;
        border: 20px solid #ADE8A9;
        border-radius: 70%;
        animation: botton 3s infinite;
    }
}
@keyframes botton {
    from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }

/* inspired https://codepen.io/AsyrafHussin/pen/KxWRrK */
// keyrames
@keyframes floating {
    from { transform: translateY(0px); }
    65%  { transform: translateY(15px); }
    to   { transform: translateY(-0px); }
}
@mixin breakpoint($point) {
    @if $point==mobile {
        @media (max-width: 480px) and (min-width: 320px) {
            @content ;
        }
    }
}
.astronaut{
    
    
    top: 18vmin;
    left: 10vmin;
    height: 30vmin;
      animation: floating 3s infinite ease-in-out;
  
    @include breakpoint(mobile){
      top: 2vmin;
    }
  }